@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap");

@tailwind base;
@tailwind components;
@tailwind utilities;

.card {
  @apply bg-white flex-col p-5 lg:hover:border-l-8  lg:border-l-4 border-b-4  hover:border-b-8 lg:hover:border-b-0 lg:border-b-0 border-primary w-full max-w-[290px] lg:max-w-[300px] 2xl:max-w-[500px] cursor-pointer hover:bg-primary hover:text-white duration-500 text-base lg:hover:-translate-x-5;
}

.card-title {
  @apply font-extrabold /*text-primary*/ text-xl lg:text-2xl 2xl:text-4xl 2xl:pb-4;
}

.card-text {
  @apply text-sm w-[70%] lg:text-base lg:w-[80%] 2xl:text-2xl;
}

.title-box {
  @apply flex justify-between items-center;
}

.service-number {
  @apply rounded-full bg-white w-12 h-12 absolute left-1/2 -translate-y-4 sm:translate-y-0 transform -translate-x-1/2 flex items-center justify-center text-primary text-3xl 2xl:text-4xl font-extrabold;
}

.service-title {
  @apply font-bold text-[1.1em] lg:text-2xl 2xl:text-4xl mb-4 text-primary;
}

.service-text {
  @apply text-sm lg:text-base mb-4 text-justify xl:text-lg 2xl:text-3xl 2xl:leading-normal;
}

.service-boxl {
  @apply lg:hover:scale-105 lg:hover:shadow-boxl lg:hover:bg-black lg:hover:-translate-x-10 lg:hover:text-white duration-500 bg-white shadow lg:p-4 pt-10 pb-5 px-5 bg-white shadow cursor-pointer;
}

.service-boxr {
  @apply lg:hover:scale-105 lg:hover:shadow-box lg:hover:bg-black lg:hover:translate-x-10 lg:hover:text-white duration-500 p-4 bg-white shadow p-4 bg-white shadow lg:p-4 py-10 px-5 cursor-pointer;
}

.socials {
  @apply hover:fill-tertiary hover:scale-125 duration-500;
}

.navlink {
  @apply hover:text-primary duration-500 uppercase;
}

.contact {
  @apply hover:bg-white hover:text-primary duration-500 bg-primary text-lg lg:text-2xl  2xl:text-5xl 2xl:py-7 font-extrabold p-2 md:p-3 w-fit mt-5 z-10 text-white hover:scale-105 hover:shadow-boxl;
}
